        }
      },

      ShowOverlay (img_src) {
        let searched = this.img_list.map(obj => {
          if ( Object.keys(obj).some( key => obj[key].indexOf(img_src) != -1 ) )
            return obj;
        });
        this.img_idx = this.img_list.indexOf(searched.filter(obj => obj != undefined)[0]);
        this.show_img = this.big_list[this.img_idx].src;
        this.ov = !this.ov;
      },
